@tailwind base;
@tailwind components;
@tailwind utilities;

.login-page-container {
    display: flex;
    height: 100vh;
  }
  
  .left-half {
    flex: 1;
    background-color: #B00040; /* Adjust color as needed */
  }
  
  .right-half {
    flex: 1;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  /* Additional styles for login form */
  form {
    width: 300px; /* Adjust width as needed */
    margin: 0 auto;
    padding: 20px;
    background-color: #f2f2f2; /* Adjust background color as needed */
    border-radius: 8px;
    .checkbox {
      display: flex;
      color: red;
      align-items: center;
      margin-bottom: 10px;
      input {
        margin-right: 5px;
      }
    }
  }
  
  h1 {
    text-align: center;
  }
  
  h4 {
    margin-bottom: 5px;
  }
  
  input {
    width: 100%;
    margin-bottom: 10px;
    padding: 8px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  
  label {
    display: block;
    margin-bottom: 10px;
  }
  
  button {
    width: 100%;
    padding: 10px;
    background-color: #B00040; /* Adjust button color as needed */
    color: #fff;
    border: none;
    border-radius: 4px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #b00041a1; /* Adjust hover color as needed */
  }
  
  a {
    text-decoration: none;
    color: #B00040;
  }
  
  a:hover {
    text-decoration: underline;
  }
  